import { formatDate } from '@angular/common';
import {
  AfterContentInit,
  AfterViewInit,
  ChangeDetectionStrategy,
  Component,
  ContentChildren,
  ElementRef,
  EventEmitter,
  Input,
  OnChanges,
  OnDestroy,
  OnInit,
  Output,
  Renderer2,
  TemplateRef,
  ViewEncapsulation,
} from '@angular/core';
import { NzDropDownDirective } from 'ng-zorro-antd';
import { Subject } from 'rxjs';
import { debounceTime } from 'rxjs/operators';
import { TableCellComponent } from './table-cell.component';
import { TableFilterComponent } from './table-filter.component';
import { TableColumn, TablePage, TableRow } from './table-interface';

declare const Viewer: any;

@Component({
  selector: 'p-table',
  exportAs: 'pTable',
  templateUrl: './table.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
})
export class TableComponent implements OnChanges, OnInit, AfterViewInit, AfterContentInit, OnDestroy {
  @Input() columns: TableColumn[] = []; // åæ°æ®
  @Input() data: { data: TableRow[]; totalSize: number } = { data: [], totalSize: 0 }; // è¡¨æ ¼æ°æ®
  @Input() selections: TableRow[] = []; // å·²éé¡¹
  @Input() scroll: { x?: string | null; y?: string | null }; // åºå®è¡¨å¤´ï¼æ»å¨
  @Input() loading = false; // è¡¨æ ¼loading
  @Input() pageSize = 10; // æ¾ç¤ºæ¡æ°
  @Input() frontPagination = false; // æ¯å¦åç«¯åé¡µ
  @Input() showPagination = true; // æ¯å¦æ¾ç¤ºåé¡µå¨
  @Input() fixedPagination = false; // æ¯å¦åºå®åé¡µå¨ï¼ç´æ¥åºå®å¨é¡µé¢åºé¨
  @Input() paginationOffset = 0; // åºå®åé¡µå¨çåç§»éï¼ç¨äºä¸åºå®å¨é¡µé¢åºé¨èä¸é¡µé¢åºé¨æå°è®¸è¾¹è·
  @Input() showSizeChanger = true; // æ¯å¦æ¾ç¤ºæ¡æ°åæ¢å¨
  @Input() showQuickJumper = true; // æ¯å¦æ¾ç¤ºå¿«éè·³è½¬å¨
  @Input() size: 'middle' | 'small' | 'default' = 'middle'; // è¡¨æ ¼size
  @Input() paginationSize: 'default' | 'small' = 'default'; // åé¡µsize
  @Input() title: string | TemplateRef<void>; // è¡¨æ ¼æ é¢
  @Input() pageSizeOptions = [10, 30, 50, 100]; // é¡µæ°éæ©å¨å¯éå¼
  @Input() showCheckbox = false; // æ¯å¦æ¾ç¤ºå¤éæ¡

  @Output() columnsChange: EventEmitter<TableColumn[]> = new EventEmitter(); // åæ°æ®æ¹åäºä»¶ ç¨äºååç»å®
  @Output() selectionsChange: EventEmitter<TableRow[]> = new EventEmitter(); // å·²éé¡¹æ¹åäºä»¶ ç¨äºååç»å®
  @Output() load: EventEmitter<TablePage> = new EventEmitter(); // loadäºä»¶
  @Output() sort: EventEmitter<{ key: string; value: 'descend' | 'ascend' | null }> = new EventEmitter(); // æåºäºä»¶
  @Output() linkClick: EventEmitter<{ field: string; rowData: any }> = new EventEmitter(); // é¾æ¥ç¹å»äºä»¶

  @ContentChildren(TableCellComponent) customCells: TableCellComponent[]; // èªå®ä¹ååæ ¼
  @ContentChildren(TableFilterComponent) customFilters: TableFilterComponent[]; // èªå®ä¹æç´¢ç»ä»¶

  load$: Subject<any> = new Subject(); // loadæµ

  displayData: TableRow[] = []; // å½åæ¾ç¤ºæ°æ®
  pageIndex = 1; // å½åé¡µç 
  sortParams: { key: string; value: 'descend' | 'ascend' | null };

  constructor(private _elementRef: ElementRef, private _renderer2: Renderer2) {}

  ngOnChanges(changes) {
    if (changes.data) {
      // éèµ°sort
      if (this.sortParams && this.sortParams.key && this.sortParams.value) {
        this.onSort(this.sortParams);
      }
    }
    if (changes.columns) {
      // æ¯ä¸æéæ©çèªå¨æ·»å è¯å¸
      this.columns.forEach(column => {
        if (column.showFilter && column.filterType === 'select' && Array.isArray(column.filterOptions)) {
          column.lexicon = column.lexicon ? [...column.lexicon, ...column.filterOptions] : column.filterOptions;
        }
      });
    }
    if (changes.selections) {
      this.updateCheckedBySelections();
    }
  }

  ngOnInit() {
    /* tslint:disable */
    this.load$.pipe(debounceTime(20)).subscribe(() => {
      // ååºloadäºä»¶
      this.load.emit({ page: this.pageIndex, size: this.pageSize });
    });
  }

  ngAfterViewInit() {
    // é¡µé¢åå§åå®æåèªå¨loadä¸æ¬¡
    this.load$.next();
    if (this.showPagination && this.fixedPagination) {
      this.toFixedPagination();
    }
  }

  ngAfterContentInit() {
    // èµå¼èªå®ä¹ååæ ¼
    this.customCells.forEach(customCell => {
      const findedColumn = this.columns.find(column => column.field === customCell.field);
      if (findedColumn) findedColumn.customCell = customCell.templateRef;
    });
    // èµå¼èªå®ä¹æç´¢ç»ä»¶
    this.customFilters.forEach(customFilter => {
      const findedColumn = this.columns.find(column => column.field === customFilter.field);
      if (findedColumn) {
        findedColumn.showFilter = true;
        findedColumn.customFilter = customFilter.templateRef;
      }
    });
  }

  ngOnDestroy() {
    // ä¸ä½¿ç¨takeUntilæ¯å ä¸ºç´æ¥unsubscribeæ§è½æ´å¥½
    this.load$.unsubscribe();
  }

  /**
   * è¡¨æ ¼å½åæ¾ç¤ºæ°æ®æ¹ååè°
   * @param currentData å½åé¡µæ¾ç¤ºæ°æ®
   */
  currentPageDataChange(currentData: any[]): void {
    this.displayData = currentData;
  }

  /**
   * åéæ¹ååè°
   */
  singleCheckChange(): void {
    this.updateSelectionsByChecked();
  }

  /**
   * å¨éå¤éæ¡æ¹ååè°
   * @param isChecked æ¯å¦å¨é
   */
  allCheckChange(isChecked: boolean): void {
    this.displayData.forEach(row => (row.isChecked = isChecked));
    this.updateSelectionsByChecked();
  }

  /**
   * æ ¹æ®checkedæ´æ°selections
   */
  updateSelectionsByChecked(): void {
    this.selections = this.displayData.filter(row => row.isChecked);
    this.selectionsChange.emit(this.selections);
  }

  /**
   * æ ¹æ®selectionsæ´æ°checked
   */
  updateCheckedBySelections(): void {
    this.displayData.forEach(row => (row.isChecked = this.selections.includes(row)));
  }

  /**
   * é¡µç æ¹ååè°
   */
  pageIndexChange(): void {
    this.load$.next();
  }

  /**
   * æ¾ç¤ºæ¡æ°æ¹ååè°
   */
  pageSizeChange(): void {
    // æ¾ç¤ºæ¡æ°æ¹åæ¶åå°é¦é¡µ
    this.pageIndex = 1;
    this.load$.next();
  }

  /**
   * æåºæ¹å
   * @param sortParams æåºåæ°
   */
  onSort(sortParams: { key: string; value: 'descend' | 'ascend' | null }): void {
    // ä¿å­æåºåæ°ï¼ç¨äºä¸æ¬¡æ°æ®è¿æ¥åè¿è¡æåº
    this.sortParams = sortParams;
    // æ¥æ¾æ­£å¨æåºçå
    const sortColumn = this.columns.find(column => column.field === sortParams.key);

    // å¦ææ²¡æèªå®ä¹æåºï¼èªå¨åç«¯æåº
    if (sortColumn && !sortColumn.customSort) {
      this.data.data.sort((previous, further) =>
        sortParams.value === 'descend'
          ? further[sortParams.key] > previous[sortParams.key]
            ? 1
            : -1
          : previous[sortParams.key] > further[sortParams.key]
          ? 1
          : -1,
      );
      this.data.data = [...this.data.data];
    }
    this.sort.emit(sortParams);
  }

  /**
   * æ¥ææ¹ååè°
   * @param isOpen æ¯å¦æå¼
   * @param column å½ååæ¨¡åæ°æ®
   */
  onRangePickerOpenChange(isOpen: boolean, column: TableColumn): void {
    if (isOpen === false) {
      const date = column.searchValue;
      if (date && Array.isArray(date) && date.length === 2) {
        column.searchValue = [formatDate(date[0], 'yyyy-MM-dd 00:00:00', 'zh_CN'), formatDate(date[1], 'yyyy-MM-dd 23:59:59', 'zh_CN')];
        column.displayValue = [formatDate(date[0], 'yyyy-MM-dd', 'zh_CN'), formatDate(date[1], 'yyyy-MM-dd', 'zh_CN')];
      }
    }
  }

  /**
   * æ¥è¯¢ç¡®è®¤åè°
   */
  onFilterConfim(dropdown: NzDropDownDirective): void {
    dropdown.nzDropdownMenu.setVisibleStateWhen(false);
    this.columns = [...this.columns];
    this.columnsChange.emit(this.columns);
  }

  /**
   * åºå®åé¡µ
   */
  toFixedPagination(): void {
    // æ²¡ææ»å¨æ¡æ¶åææ»å¨æ¡æ¶tableBodyä¼ä¸ä¸æ ·ï¼æåç»ä¸æ»å¨æ¡
    this.scroll = { ...this.scroll, y: '0px' };
    // ç­å¾æ»å¨æ¡æ´æ°
    setTimeout(() => {
      const windowHeight = document.documentElement.clientHeight;
      const tableBody = this._elementRef.nativeElement.querySelector('.ant-table-body');
      const pagination = this._elementRef.nativeElement.querySelector('.p-table-pagination-container');
      const tableBodyTop = tableBody.getBoundingClientRect().top;
      const scrollHeight = windowHeight - tableBodyTop - pagination.clientHeight - this.paginationOffset + 'px';
      // è®¾scroll å®éä¸æ¯è®¾äºmax-height
      this.scroll = { ...this.scroll, y: scrollHeight };
      // è®¾height
      this._renderer2.setStyle(tableBody, 'height', scrollHeight);
    });
  }

  onlinkClick(field: string, rowData: any) {
    this.linkClick.emit({ field, rowData });
  }

  view(imgUrls: string[]) {
    const images = imgUrls.map(url => {
      const image = new Image();
      image.src = url;
      return image;
    });

    const hostElement = document.createElement('div');
    hostElement.append(...images);

    const viewer = new Viewer(hostElement, {
      hidden: () => {
        viewer.destroy();
      },
    });
    viewer.show();
  }
}
